body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #f8f9fa;
  transition: background 0.3s, color 0.3s;
}

.App {
  text-align: center;
  padding: 20px;
  min-height: 100vh;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.theme-toggle {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
}

.theme-toggle:hover {
  background-color: #0056b3;
}

form {
  margin: 20px 0;
}

input {
  margin: 5px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

button {
  margin: 5px;
  padding: 8px 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
}

button:hover {
  background-color: #0056b3;
}

.clear-btn {
  background-color: #dc3545;
  color: white;
  margin-bottom: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #007bff;
  color: white;
}

/* ðŸŒ™ Dark Mode Styles */
.App.dark {
  background-color: #121212;
  color: #f1f1f1;
}

.App.dark table {
  border-color: #333;
}

.App.dark th {
  background-color: #333;
}

.App.dark td {
  background-color: #1e1e1e;
}

.App.dark input,
.App.dark button {
  background-color: #333;
  color: #fff;
  border: 1px solid #555;
}

.App.dark .theme-toggle {
  background-color: #f1c40f;
  color: #000;
}
